.header {
  position: absolute;
  top: 0;
  z-index: 5;
  width: 100%;

  margin-top: 2.6rem;
}

.bg.header {
  position: absolute;

  .header__wrapper {
    margin-bottom: 0;
    padding-bottom: 0;
    border-bottom: none;
  }
}

.header__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;

  // margin-bottom: 3.6rem;
  // padding-bottom: 2.2rem;
}

.header__logo {
  // margin-right: 25px;
}

.header__phone-wrapper {
  text-align: right;
}

.header__phone {
  display: inline-block;

  font-size: 2.2rem;
  font-weight: @heavy;
}

.header__phone-text {
  font-size: 1.7rem;
}

.header__menu-toggle,
.header__link-call-back {
  display: none;
}

