.btn {
  box-sizing: border-box;
  position: relative;

  display: flex;
  align-items: center;

  width: 232px;
  height: 58px;

  color: #fff;
  font-weight: @medium;
  text-transform: uppercase;

  border-radius: 29px;
  background-image: linear-gradient(to left, #2bae60 0%, #1a944b 100%);

  &--icon {
    padding-left: 27px;
    font-size: 17px;
    &::before {
      content: '';
      position: absolute;
      top: 50%;
      right: 10px;
      transform: translateY(-50%);

      width: 32px;
      height: 32px;
      background-color: #fff;
      border-radius: 50%;
    }
    &::after {
      content: '';
      position: absolute;
      top: 50%;
      right: 22px;
      transform: translateY(-50%);

      width: 7px;
      height: 12px;
      background-image: url('../img/svg/arrow-short-green.svg');
      background-repeat: no-repeat;
      background-position: center;
      background-size: contain;
    }
  }

  &--icon-tel {
    width: 220px;
    justify-content: center;

    font-weight: @heavy;
    // font-size: 20px;
    font-size: 19px;
  }

  // отступ от макета, т.к. заполненное поле получается больше
  &--tel {
    // width: 257px;
    width: 262px;
    padding-left: 20px;

    color: @color-d-t;
    // font-size: 20px;
    font-size: 19px;
    background-color: @color-d-s;

    background-image: url('../img/svg/tel-circle-green.svg');
    background-repeat: no-repeat;
    background-position: 96% center;
  }
}

